import react from 'react'
import {NavLink} from "react-router-dom"

const Navbar = () =>{
    return(
        <>
        <div className="container-fluid nav-bg" >
          <div className='row'>
            <div className='col-14 mx-auto'>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <NavLink className="navbar-brand" to="/">Quatorze</NavLink>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">
      <li className="nav-item active">
        <NavLink exact activeClassName='menu-active' 
         className="nav-link" to="/">Home <span class="sr-only">(current)</span></NavLink>
      </li>
      <li className="nav-item">
        <NavLink  activeClassName='menu-active'
         className="nav-link" to="/About">
          About
          </NavLink>
      </li> 
      <li className="nav-item">
        <NavLink  activeClassName='menu-active'
         className="nav-link" to="/Contact">
          Contact
          </NavLink>
      </li> 
      <li className="nav-item">
        <NavLink  activeClassName='menu-active'  
        className="nav-link" to="Service">
          Service
          </NavLink>
      </li>
      
    </ul>
    
  </div>
</nav>
             
          </div>
          </div>
        </div>
        </>
    );
};
export default Navbar;